html,
body {
  height: 100%;
}

body {
  background-color: #000;
}

.slider {
  height: 100%;
}

.slider__layer {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  will-change: transform;
}
.slider__layer:before {
  content: "";
  position: absolute;
  inset: 0;
  z-index: 1;
  box-shadow: inset 0 0 250px rgba(0, 0, 0, 0.3);
}

.slider__item {
  overflow: hidden;
}

.slider__wrapper {
  will-change: transform;
  transform-style: preserve-3d;
}